//#   variables
//#   
:root{
    //  wordpressfunc(../)
    //  (../../)URL
    --dir-img : "../img/";
    //  URL
    --bgurl-about     : url("../img/about/bg.png" );
    --bgurl-about-webp: url("../img/about/bg.webp");
    --bgurl-flow      : url("../img/flow/bg.jpg" );
    --bgurl-flow-webp : url("../img/flow/bg.webp");
    --bgurl-our       : url("../img/our/bg.jpg" );
    --bgurl-our-webp  : url("../img/our/bg.webp");
    --bgurl-befworrie : url("../img/worries/bef_worrie@2x.png");
}
// 
$layout-width-comp:1280px; //  
$layout-width-inner:1080px;//    : 
$layout-width-inner-lg:1000px;//    : 
$layout-width-inner-md:700px;//    : 
$layout-width-comp-sp:375px; //  -sp
$layout-width-inner-sp:calc( 100% - 40px );//    : sp
$header-height:80px;
$header-height-sp:60px;

//  
$header-second-height-sp:0px;  //  0

$imgdir : "img";    // 
$pimgdir : "../img";   //  


$numberFont: 'Roboto';
$font-Futura: 'Hind'; //  Futura

// googlefont
$font-roboto: 'Roboto';
$font-Lato: 'Lato';
$font-Lora: 'Lora';
$font-Allura: 'Allura';
$font-KiwiMaru: 'Kiwi Maru';
$font-KosugiMaru: 'Kosugi Maru';
$font-MPlus1p: 'M PLUS 1p';
$font-MPlusRounded1c: 'M PLUS Rounded 1c';
$font-YuMincho: 'Yu Mincho';
$font-Josefin: 'Josefin Sans';
$font-Montserrat: 'Montserrat';
$font-Exo: 'Exo';
$font-Bangla-mn: 'Bangla MN';

$baseFont: $font-MPlusRounded1c, "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;

// 
$font-eng: $font-Montserrat, $baseFont;
$font-title: $font-YuMincho, $baseFont;
$font-lead: $baseFont;
$font-number: $font-Futura, $baseFont;
